@import 'color';
// @import 'layout';

* {
    font-family: 'Noto Sans TC', sans-serif;
    color: $color-main;
    box-sizing: border-box;
    white-space: nowrap;
}

body {
    margin: 0;
    padding: 0;
    margin-top: 6rem;
}

h1 {
    font-weight: 300;
    
}

h2, h3, h4, h5, h6, p, li, a, span {
    font-weight: 300;
    color: rgb(13, 85, 114);
}

a {
    text-emphasis: none;
    text-decoration: none;
}

@mixin sepration-line{
    &::after {
        content: "";
        display: block;
        width: 90%;
        height: 2px;
        background-color: $color-main;
        margin: 20px 5%;
    }
}

