* {
  font-family: 'Noto Sans TC', sans-serif;
  color: #00416b;
  box-sizing: border-box;
  white-space: nowrap; }

body {
  margin: 0;
  padding: 0;
  margin-top: 6rem; }

h1 {
  font-weight: 300; }

h2, h3, h4, h5, h6, p, li, a, span {
  font-weight: 300;
  color: #0d5572; }

a {
  text-emphasis: none;
  text-decoration: none; }

@media (min-width: 1281px) {
  .block-margin {
    margin: 0 auto; }
    .block-margin.wide {
      max-width: 1920px;
      width: 80vw;
      min-width: 1100px; }
    .block-margin.narrow {
      width: 900px; } }

@media (min-width: 769px) and (max-width: 1280px) {
  .block-margin {
    margin: 0 auto; }
    .block-margin.wide {
      width: 85vw;
      min-width: 668px; }
    .block-margin.narrow {
      width: 896px; } }

@media (min-width: 769px) and (max-width: 1024px) {
  .block-margin {
    margin: 0 50px; } }

@media (min-width: 320px) and (max-width: 768px) {
  .block-margin {
    margin: 0 28px; } }

.main-section-margin {
  margin-bottom: 5rem; }

.footer {
  background-color: #00416b;
  position: inherit; }
  .footer > div {
    padding: 0.5rem 0;
    position: inherit;
    display: flex;
    justify-content: space-between;
    align-items: flex-end; }
  .footer li {
    position: relative;
    display: inline-block;
    list-style-type: none;
    vertical-align: middle;
    margin-right: 3rem; }
  .footer .logo {
    display: inline-block; }
    .footer .logo img {
      height: 4rem; }
  .footer .footer-nav * {
    color: white; }

.filter > .filter-masking {
  filter: grayscale(100%);
  transition: 0.5s; }

.filter::after {
  z-index: 2;
  position: absolute;
  content: '';
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #c0c0c0;
  opacity: 0.4;
  transition-duration: 0.5s; }

.filter .fade-in {
  transition-duration: 0.5s;
  opacity: 0; }

.filter:hover > .filter-masking {
  filter: grayscale(0%) brightness(50%) contrast(119%); }

.filter:hover::after {
  opacity: 0; }

.filter:hover .fade-in {
  opacity: 1; }

.header {
  z-index: 10000;
  background-color: rgba(255, 255, 255, 0.9);
  position: fixed;
  width: 100vw;
  top: 0;
  left: 0; }
  .header > div {
    height: 6rem;
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .header .logo {
    display: inline-block; }
    .header .logo img {
      height: 4rem;
      width: 4rem; }

.nav {
  position: inherit;
  display: flex;
  align-items: center; }
  .nav .nav-bar {
    overflow: hidden;
    line-height: 1.7rem;
    vertical-align: middle; }
    .nav .nav-bar ul {
      margin: 0;
      padding: 0;
      position: relative;
      display: inline-block;
      text-transform: capitalize;
      transition: .8s;
      transform: translate(10%, 0); }
    .nav .nav-bar li {
      position: relative;
      display: inline-block;
      list-style-type: none;
      vertical-align: middle;
      margin-right: 3rem; }
      .nav .nav-bar li a::after {
        content: "";
        position: absolute;
        display: block;
        left: 0;
        bottom: -0.4rem;
        width: 0%;
        border-bottom: solid 2px #EC6C1F;
        transition-duration: 0.5s; }
      .nav .nav-bar li:hover a::after {
        width: 100%; }
      .nav .nav-bar li a {
        position: relative;
        display: inline-block;
        text-decoration: none; }
        .nav .nav-bar li a::after {
          bottom: 0; }
  .nav .nav-icon {
    display: inline-block;
    height: 2.4rem;
    width: 2.4rem;
    margin-left: 1rem; }
    .nav .nav-icon div {
      transition-duration: 1s;
      position: absolute;
      height: 0.2rem;
      width: 2rem;
      background-color: #00416b;
      transform-origin: center;
      top: 50%; }
      .nav .nav-icon div:nth-child(1) {
        transform: translate(0, -0.7rem); }
      .nav .nav-icon div:nth-child(2) {
        transform: translate(0, 0); }
      .nav .nav-icon div:nth-child(3) {
        transform: translate(0, 0.7rem); }
  .nav.open .nav-icon div:nth-child(1) {
    transform: translate(0, 0) rotate(45deg); }
  .nav.open .nav-icon div:nth-child(2) {
    transform: scale(0.0001, 1); }
  .nav.open .nav-icon div:nth-child(3) {
    transform: translate(0, 0) rotate(-45deg); }
  .nav.open .nav-bar ul {
    transform: translate(0, 0); }

.page-title {
  position: relative; }
  .page-title h1 {
    position: inherit;
    font-weight: 500;
    text-align: center;
    text-transform: capitalize;
    margin: auto 0;
    top: 0;
    bottom: 0;
    line-height: 8rem;
    font-size: 2rem; }
    .page-title h1::after {
      content: '';
      display: inline-block;
      background-image: url(../../../asset/asterisk.svg);
      height: 0.5em;
      width: 0.5em;
      margin: 5px; }

.projects {
  position: relative; }
  .projects * {
    position: inherit; }
  .projects .project-img {
    margin: 0;
    flex: auto 1 1;
    height: 290px;
    width: 100%; }
    .projects .project-img img {
      width: 100%;
      height: 100%; }
  .projects .project-content {
    display: flex;
    height: 11%;
    padding: 1rem 0.5rem;
    margin: 0; }
    .projects .project-content .brand {
      margin: 0;
      margin-top: 2px; }
    .projects .project-content .line {
      position: relative;
      margin: 3px 5px 0 -1px; }
      .projects .project-content .line svg {
        position: relative;
        width: 3rem;
        top: 5px;
        bottom: 0; }
    .projects .project-content .title {
      text-align: left;
      align-self: flex-end;
      padding: 0px 0 10px 0; }
      .projects .project-content .title * {
        margin: 0; }

